@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@900&display=swap);
body{font-family:"IBM Plex Mono", monospace;padding:0;margin:0;background:linear-gradient(to right, #ebeff1, #bac7c2, #d6d0da, #bac7c2, #c7abda, #bac7c2, #ebeff1)}.logo h1{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;color:white;width:2rem;height:2rem;border-radius:50%;background-image:linear-gradient(to left, #9b769b, #c7abda, #c4e2d1);position:absolute;left:1rem;font-family:"Dancing Script", cursive}.img-slider img{width:50%;height:50%;border-radius:10%}.img-slider{display:block}.home{padding:2rem}.GalaryFoto{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly}li:nth-child(2n){-webkit-flex-direction:row-reverse;flex-direction:row-reverse}.GalaryFoto .picture{padding:1rem;width:18rem;height:15rem;border-radius:10%}.GalaryFoto img:hover{box-shadow:10px 5px 5px #646363}.main-page{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;justify-items:center;padding:1rem}.link-button{background-image:linear-gradient(to left, #9e8d9e, #c7abda, #b1beb7);width:10rem;height:2rem}.link-button:hover{background-image:linear-gradient(to left, #c0b1c0, #a9a1af, #7e9688);color:white;border:2px solid #00cec9}.menu,.contact{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:row;flex-direction:row;justify-items:center;width:100vw;height:7vh;background-color:azure}.footer{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;position:fixed;bottom:0rem;background-color:rgba(245,245,245,0.377);width:100vw;height:7vh}.footer li:hover{color:rgba(170,132,132,0.781)}li{padding:1rem;list-style:none}a{text-decoration:none}.about,.home,.skills{height:83vh}.about{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;justify-items:center;padding:5rem;width:60%;height:70%}.about-text h1{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row}.about h2,h3{color:#581950}.words,.skill-word{line-height:1.5;color:#581950;font-size:2rem;padding:3rem;display:inline-block;-webkit-animation:move 3s ease-in-out infinite;animation:move 3s ease-in-out infinite}@-webkit-keyframes move{0%{-webkit-transform:translate(-30%, 0);transform:translate(-30%, 0)}50%{text-shadow:0 25px 50px rgba(0,0,0,0.75)}100%{-webkit-transform:translate(30%, 0);transform:translate(30%, 0)}}@keyframes move{0%{-webkit-transform:translate(-30%, 0);transform:translate(-30%, 0)}50%{text-shadow:0 25px 50px rgba(0,0,0,0.75)}100%{-webkit-transform:translate(30%, 0);transform:translate(30%, 0)}}.skills{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;padding:1rem;color:#581950}.contactMe{padding-left:25%;width:40%}.contactMe div{padding:1rem;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;justify-items:center;-webkit-flex-direction:column;flex-direction:column}.year{display:-webkit-flex;display:flex;color:#581950;position:fixed;left:1rem}.email{display:-webkit-flex;display:flex;color:#581950;position:fixed;right:1rem}@media only screen and (max-width: 1240px){.footer,.address,.contact,.menu{background-color:transparent;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;text-align:center;padding:1rem;margin:1rem}.footer .footer,.address .footer,.contact .footer,.menu .footer{background-color:rgba(245,245,245,0.377)}.footer .home,.address .home,.contact .home,.menu .home{display:block}}

